import document from '@/api/document'
// import axios from "axios";

// initial state
const state = {
  document: {
    title: '',
    sections: []
  },
  current_chapter: {
    identifier: '',
    text: ''
  },
  current_subchapter: null,
  current_paragraph: null
};

// mutations
const mutations = {
  SET_DOCUMENT: (state, payload) => {
    state.document.title = payload.title;
    state.document.sections = payload.sections;
  },
  UPDATE_CHAPTER: (state) => {
    state.document.sections.forEach(
      function (chapter) {
        if (chapter.id == state.current_chapter.identifier) {
          chapter.title = state.current_chapter.text;
        }
      }
    );
    state.current_chapter.identifier = '';
    state.current_chapter.text = '';
  },
  UPDATE_CURRENT_CHAPTER: (state, payload) => {
    state.current_chapter.identifier = payload.id;
    state.current_chapter.text = payload.title;
  }
}

// getters
const getters = {
    title(state) {
      return state.document.title;
    },
    content(state) {
      return state.document.sections;
    },
    hasContent(state) {
      return state.document.title !== "";
    }
}

// actions
const actions = {
  fetchDocument ({ commit }) {
    document.getDocument(document => {
      commit('SET_DOCUMENT', document)
    });
  },
  updateCurrentChapter (context, payload) {
    context.commit('UPDATE_CURRENT_CHAPTER', payload);
    context.commit('UPDATE_CHAPTER');
  },
  // async backendDocument({ commit }) {
  //   try {
  //     const data = await axios.get(
  //       "http://localhost:9980/api/pfc/getContent"
  //     );
  //     commit("SET_DOCUMENT", data.document);
  //   } catch (error) {
  //     console.log(error);
  //   }
  // }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}