<template>
  <div>
    <b-list-group-item href="#" class="flex-column align-items-start">
      <div class="d-flex w-100 justify-content-between">
        <h3 class="mb-1" v-if="!isEditable">{{ newText }}</h3>
        <b-button size="sm" @click="editChanged()" class="mr-1" variant="outline-secondary" v-if="!isEditable"><b-icon-pencil/></b-button>
        <b-form-input v-model="newText" v-if="isEditable"></b-form-input>
        <b-button size="sm" @click="changeTitle(newText)" class="mr-1" variant="primary" v-if="isEditable"><b-icon-check-square/></b-button>
        <b-button size="sm" @click="editChanged()" class="mr-1" variant="outline-secondary" v-if="isEditable"><b-icon-x-square/></b-button>        
      </div>
      
      <SubchapterPfc
        v-for="subsection in subsections"
        :key="subsection.id"
        v-bind="subsection"
      />

    <div class="d-flex w-100 justify-content-between">
      <h4 class="mb-1" v-if="!enabledAdd">{{ newSubsection }}</h4>
      <b-form-input v-model="newSubsection" v-if="enabledAdd"></b-form-input>
      <b-button size="sm" @click="addSubsection(newSubsection)" class="mr-1" variant="primary" v-if="enabledAdd"><b-icon-check-square/></b-button>
      <b-button size="sm" @click="addChanged()" class="mr-1" variant="outline-secondary" v-if="enabledAdd"><b-icon-x-square/></b-button>   
      <b-button size="sm" @click="addChanged()" class="mr-1" variant="primary" v-if="!enabledAdd">Añadir<b-icon-plus/></b-button>
    </div>
    </b-list-group-item>
  </div>
</template>

<script>
import SubchapterPfc from './SubsectionPFC.vue'

export default {
  name: 'ChapterPfc',
  components: {
    SubchapterPfc
  },
  props: {
    id: String,
    title: String,
    subsections: Array
  },
  data() {
    return {
      isEditable: false,
      newText: '',
      enabledAdd: false,
      newSubsection: '¿Quieres añadir una nueva sección?',
    }
  },
  mounted() {
    this.newText = this.title
  },
  computed: {
    targetTitle() {
      return this.title
    }
  },
  methods: {
    changeTitle(payload) {
      this.isEditable = !this.isEditable;
      var currentSection = {
        id: this.id,
        title: payload
      }
      this.$store.dispatch('pfc/updateCurrentChapter', currentSection);
      this.$emit("update-new-text", payload) ;
    },
    editChanged() {
      this.isEditable = !this.isEditable;
    },
    addChanged() {
      this.enabledAdd = !this.enabledAdd;
    },
    addSubsection(payload) {
      console.log(payload);
    }
  },
}
</script>

